<template>
  <div class="TabelaTel">
    <div class="col">
      <input
        v-model="szukane"
        type="text"
        class="form-control-dark"
        style="margin: 10px"
        placeholder="Wyszukaj Osoby"
      />
    </div>
    <table class="table table-bordered table-striped table-dark">
      <thead>
        <tr>
          <th scope="col">Imie</th>
          <th scope="col">Nazwisko</th>
          <th scope="col">Numer Telefonu</th>
          <th scope="col">Edytowanie</th>
        </tr>
      </thead>
      <tbody>
        <WierszTabela
          v-for="k in listaOsob"
          :key="k.id"
          :daneosoba="k"
          @usun="$emit('usuniencie', k)"
          @zapisz="$emit('zapisywanie', k)"
        />
      </tbody>
    </table>
  </div>
</template>

<script>
import WierszTabela from "../osoba/WierszOsob.vue";

export default {
  components: {
    WierszTabela,
  },
  data() {
    let szukane = "";
    return {
      szukane,
    };
  },
  methods: {
    walonefiltrowanie(obiekt) {
      return (
        obiekt.Imie.includes(this.szukane) ||
        obiekt.Nazwisko.includes(this.szukane) ||
        obiekt.Telefon.includes(this.szukane)
      );
    },
  },
  name: "TabelaOsob",
  props: ["listaOsob"],
};
</script>
