<template>
  <div class="bg-dark container" id="app">
    <div class="row" style="text-align: center" v-if="uzytk == false">
      <div class="col-sm-4"></div>
      <div class="col-sm-4">
        <div class="form-group">
          <label>Email</label>
          <input
            type="text"
            v-model="loginUser.email"
            class="form-control"
            placeholder="Email"
          />
        </div>

        <div class="form-group">
          <label>Password</label>
          <input
            type="password"
            v-model="loginUser.password"
            class="form-control"
            placeholder="Hasło"
          />
        </div>

        <button type="button" v-on:click="logowanie()" class="btn btn-success">
          Login
        </button>
        <button
          type="button"
          v-on:click="logowanieGoogle()"
          class="btn btn-success"
        >
          Login with Google
        </button>
      </div>
    </div>


    <!-- TO JEST TUTAJJJ -->
    <!-- TO JEST TUTAJJJ -->
    <!-- TO JEST TUTAJJJ -->
    
    <div v-else-if="zbiorkadodawanie == false && edycjaOSOB==false && edycjaZBIOREK==false">

      <TabelkaZbiorki
        :skladka="skladka"
        :listaOsob="osoby"
        @wejscie="wejscieZbiorka"
      />

      <button type="button" v-on:click="edycjaOSOB=true" class="btn btn-success">
          Edycja osób
        </button>

      <button type="button" v-on:click="edycjaZBIOREK=true" class="btn btn-success">
          Edycja Zbiórek
        </button>
      
    </div>

    <div v-else-if="zbiorkadodawanie == true && edycjaOSOB==false && edycjaZBIOREK==false">

      <TabelaWplat
        :listaOsob="osoby"
        :wplaty="Wplaty"
        :wplatySkladka="skladkacopy"
        @zaplacil="placi"
        @usun="usuwanie"
        @edytowanie="zbiorkaedycja"
      />
      <button
        type="button"
        v-on:click="zbiorkadodawanie = false"
        class="btn btn-success"
      >
        Wróc
      </button>
    </div>

    

    <div v-else-if="zbiorkadodawanie == false && edycjaOSOB==false && edycjaZBIOREK==true">
      <TabelaSkladki
        :skladka="skladka"
        @usuniencie="skladkausuwanie"
        @zapisywanie="skladkaedycja"
      />
      <WpisywanieSkladki :danezbiorki="zbiorka" @dodaj="skladkadodawanie" />

      <button
        type="button"
        v-on:click="zbiorkadodawanie = false, edycjaOSOB=false, edycjaZBIOREK=false"
        class="btn btn-success"
      >
        Wróc
      </button>

    </div>

    <div v-else-if="zbiorkadodawanie == false && edycjaOSOB==true && edycjaZBIOREK==false">
      <TabelaOsob
        :listaOsob="osoby"
        @usuniencie="usuwanieosoby"
        @zapisywanie="edycjaosoby"
      />
      <WpisywanieOsob :daneosoba="osoba" @dodaj="dodawanieosoby" />
      
      <button
        type="button"
        v-on:click="zbiorkadodawanie = false, edycjaOSOB=false, edycjaZBIOREK=false"
        class="btn btn-success"
      >
        Wróc
      </button>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from "vue";
import {
  collection,
  doc,
  onSnapshot,
  addDoc,
  updateDoc,
  deleteDoc,
} from "firebase/firestore";
import WpisywanieSkladki from "./components/skladki/WpisywanieSkladki.vue";
import TabelaSkladki from "./components/skladki/TabelaSkladki.vue";
import WpisywanieOsob from "./components/osoba/WpisywanieOsob.vue";
import TabelaOsob from "./components/osoba/TabelaOsob.vue";
import TabelkaZbiorki from "./components/zbiorki/TabelkaZbiorki.vue";
import TabelaWplat from "./components/zbiorki/tabela.vue";
import { db, provider } from "@/firebase";
import { Timestamp } from "firebase/firestore";
import {
  signInWithEmailAndPassword,
  getAuth,
  signInWithPopup,
  GoogleAuthProvider,
} from "firebase/auth";
export default {
  name: "App",
  components: {
    WpisywanieSkladki,
    TabelaSkladki,
    WpisywanieOsob,
    TabelaOsob,
    TabelkaZbiorki,
    TabelaWplat,
  },

  setup() {
    var skladka = ref([]);
    var osoby = ref([]);
    var Wplaty = ref([]);
    onMounted(async () => {
      if (getAuth() != null) {
        onSnapshot(collection(db, "skladki"), (querySnapshot) => {
          const wszystkieskladki = [];
          querySnapshot.forEach((doc) => {
            wszystkieskladki.push({ id: doc.id, ...doc.data() });
          });
          skladka.value = wszystkieskladki;
        });

        onSnapshot(collection(db, "WszystkieOsoby"), (querySnapshot) => {
          const wszystkieOsoby = [];

          querySnapshot.forEach((doc) => {
            wszystkieOsoby.push({ id: doc.id, ...doc.data() });
          });
          osoby.value = wszystkieOsoby;
        });

        onSnapshot(collection(db, "Wplaty"), (querySnapshot) => {
          const WsyskieWplaty = [];
          querySnapshot.forEach((doc) => {
            WsyskieWplaty.push({ id: doc.id, ...doc.data() });
          });
          Wplaty.value = WsyskieWplaty;
        });
      }
    });

    return {
      skladka,
      osoby,
      Wplaty,
    };
  },

  data() {
    const zbiorkadodawanie = false;
    const uzytk = false;
    var loginUser = {
      email: "",
      password: "",
    };

    var skladkacopy;

    const edycjaOSOB = false;
    const edycjaZBIOREK = false;

    var zbiorka = {
      Nazwa: "",
      KwotaOdOsoby: "",
      IleDoZebrania: "",
      Data: "",
    };

    var wplata = {
      data: "",
      uczen_id: "",
      skladka_id: "",
      kwota: "",
    };

    var osoba = {
      Imie: "",
      Nazwisko: "",
      Telefon: "",
    };
    return {
      edycjaOSOB,
      edycjaZBIOREK,
      zbiorka,
      loginUser,
      osoba,
      uzytk,
      zbiorkadodawanie,
      wplata,
      skladkacopy,
    };
  },

  methods: {
    // AUTORYZACJA

    logowanie() {
      const auth = getAuth();
      console.log(auth);
      signInWithEmailAndPassword(
        auth,
        this.loginUser.email,
        this.loginUser.password
      )
        .then((userCredential) => {
          // Signed in
          const user = userCredential.user;
          this.uzytk = true;
          console.log(user);
        })
        .catch((error) => {
          const errormessage = error.message;
          alert(errormessage);
          console.log(error.Code);
        });
    },

    logowanieGoogle() {
      console.log(provider);
      const auth = getAuth();
      console.log(auth);
      signInWithPopup(auth, provider)
        .then((result) => {
          // This gives you a Google Access Token. You can use it to access the Google API.
          const credential = GoogleAuthProvider.credentialFromResult(result);
          const token = credential.accessToken;
          // The signed-in user info.
          this.uzytk = true;
          const user = result.user;
          // IdP data available using getAdditionalUserInfo(result)
          // ...
          console.log(token);
          console.log(user);
        })
        .catch((error) => {
          // Handle Errors here.
          const errorCode = error.code;
          const errorMessage = error.message;
          // The email of the user's account used.
          const email = error.customData.email;
          // The AuthCredential type that was used.
          const credential = GoogleAuthProvider.credentialFromError(error);
          // ...
          console.log(errorCode);
          console.log(errorMessage);
          console.log(email);
          console.log(credential);
        });
    },

    // SKLADKA

    skladkadodawanie(obiekt) {
      addDoc(collection(db, "skladki"), {
        Nazwa: obiekt.Nazwa,
        KwotaOdOsoby: obiekt.KwotaOdOsoby,
        IleDoZebrania: obiekt.IleDoZebrania,
        Data: Timestamp.now().toDate().toLocaleString("pl-PL"),
      });
      console.log(obiekt);
      obiekt.Nazwa = "";
      obiekt.KwotaOdOsoby = "";
      obiekt.IleDoZebrania = "";
      obiekt.Data = "";
    },
    skladkausuwanie(obiekt) {
      console.log(obiekt.uId);
      if (confirm("Czy chcesz usunąć tą składke")) {
        deleteDoc(doc(db, "skladki", obiekt.id));
        alert("Usunięto");
      }
    },

    skladkaedycja(obiekt) {
      // console.log(obiekt);
      updateDoc(doc(db, "skladki", obiekt.id), {
        Nazwa: obiekt.Nazwa,
        KwotaOdOsoby: obiekt.KwotaOdOsoby,
        IleDoZebrania: obiekt.IleDoZebrania,
      });
      console.log(obiekt);
    },

    // OSOBY

    dodawanieosoby(obiekt) {
      addDoc(collection(db, "WszystkieOsoby"), {
        Imie: obiekt.Imie,
        Nazwisko: obiekt.Nazwisko,
        Telefon: obiekt.Telefon,
      });
      console.log(obiekt);
      obiekt.Imie = "";
      obiekt.Nazwisko = "";
      obiekt.Telefon = "";
    },
    usuwanieosoby(obiekt) {
      // console.log(obiekt);
      if (confirm("Czy chcesz usunąć ten kontakt")) {
        deleteDoc(doc(db, "WszystkieOsoby", obiekt.id));
        alert("Usunięto");
      }
    },

    edycjaosoby(obiekt) {
      // console.log(obiekt);
      updateDoc(doc(db, "WszystkieOsoby", obiekt.id), {
        Imie: obiekt.Imie,
        Nazwisko: obiekt.Nazwisko,
        Telefon: obiekt.Telefon,
      });
      console.log(obiekt);
    },
    // ZBIORKI

    wejscieZbiorka(obiekt) {
      this.skladkacopy = obiekt;
      this.zbiorkadodawanie = true;
    },

    placi(osoba, skladka, kwotax) {
      addDoc(collection(db, "Wplaty"), {
        data: Timestamp.now().toDate().toLocaleString("pl-PL"),
        kwota: kwotax,
        skladka_id: skladka.id,
        uczen_id: osoba.id,
      });
    },

    usuwanie(obiekt) {
      deleteDoc(doc(db, "Wplaty", obiekt.id));
      alert("Usunięto");
    },

    zbiorkaedycja(obiekt, xkwota) {
      // console.log(obiekt);
      updateDoc(doc(db, "Wplaty", obiekt.id), {
        kwota: xkwota,
        data: Timestamp.now().toDate().toLocaleString("pl-PL"),
      });
      console.log(obiekt);
    },
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  margin-top: 60px;
  margin-bottom: 60px;
}
</style>
