<template>
  <div class="TabelaTel">
    <div class="col">
      <input
        v-model="szukane"
        type="text"
        class="form-control-dark"
        style="margin: 10px"
        placeholder="Wyszukaj Składki "
      />
    </div>
    <table class="table table-bordered table-dark">
      <tbody>
        <WierszZbiorki
          v-for="k in skladka.filter(walonefiltrowanie)"
          :key="k.id"
          :daneskladka="k"
          @wejdz="$emit('wejscie', k)"
        />
      </tbody>
    </table>
  </div>
</template>

<script>
import WierszZbiorki from "./WierszZbiorki.vue";

export default {
  components: {
    WierszZbiorki,
  },
  data() {
    let szukane = "";
    return {
      szukane,
    };
  },
  methods: {
    walonefiltrowanie(object) {
      return (
        object.Nazwa.includes(this.szukane) ||
        object.IleDoZebrania.includes(this.szukane) ||
        object.KwotaOdOsoby.includes(this.szukane)
      );
    },
  },
  name: "TabelaZbiorki",
  props: ["skladka", "listaOsob"],
};
</script>
