<template>
  <div class="TabelaTel">
    <div class="col">
      <input
        v-model="szukane"
        type="text"
        class="form-control-dark"
        style="margin: 10px"
        placeholder="Wyszukaj Składki "
      />
    </div>
    <table class="table table-bordered table-striped table-dark">
      <thead>
        <tr>
          <th scope="col">Nazwa</th>
          <th scope="col">Ile do zebrania</th>
          <th scope="col">Kwota od osoby</th>
          <th scope="col">Data</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <WierszSkladki
          v-for="k in skladka.filter(walonefiltrowanie)"
          :key="k.id"
          :daneskladka="k"
          @usun="$emit('usuniencie', k)"
          @zapisz="$emit('zapisywanie', k)"
        />
      </tbody>
    </table>
  </div>
</template>

<script>
import WierszSkladki from "./WierszSkladki.vue";

export default {
  components: {
    WierszSkladki,
  },
  data() {
    let szukane = "";
    return {
      szukane,
    };
  },
  methods: {
    walonefiltrowanie(object) {
      return (
        object.Nazwa.includes(this.szukane) ||
        object.IleDoZebrania.includes(this.szukane) ||
        object.KwotaOdOsoby.includes(this.szukane) 
      );
    },
  },
  name: "TabelaSkladki",
  props: ["skladka"],
};
</script>
