<template>
  <tr v-if="edytowanie == false" style="margin: 10px">
    <td>{{ daneskladka.Nazwa }}</td>
    <td>{{ daneskladka.IleDoZebrania }}</td>
    <td>{{ daneskladka.KwotaOdOsoby }}</td>
    <td>{{ daneskladka.Data }}</td>
    <td>
      <button
        type="button"
        class="btn btn-sm btn-dark"
        style="margin: 10px"
        v-on:click="$emit('usun')"
      >
        Usuń
      </button>
      <button
        type="button"
        class="btn btn-sm btn-dark"
        style="margin: 10px"
        v-on:click="(edytowanie = true), (daneZbiorki = daneskladka)"
      >
        Edytuj
      </button>
    </td>
  </tr>

  <tr class="info" v-else>
    <td>
      <input
        v-model="daneZbiorki.Nazwa"
        type="text"
        class="form-control-dark"
        style="margin: 10px"
        placeholder="Nazwa"
      />
    </td>
    <td>
      <input
        v-model="daneZbiorki.IleDoZebrania"
        type="text"
        class="form-control-dark"
        style="margin: 10px"
        placeholder="Ilość do zebrania"
      />
    </td>
    <td>
      <input
        v-model="daneZbiorki.KwotaOdOsoby"
        type="text"
        class="form-control-dark"
        style="margin: 10px"
        placeholder="Kwota od osoby"
      />
    </td>
    <td>
      <button
        type="button"
        class="btn btn-sm btn-dark"
        style="margin: 10px"
        v-on:click="$emit('zapisz', daneZbiorki), (edytowanie = false)"
      >
        Zapisz
      </button>
    </td>
    <td></td>
  </tr>
</template>

<script>
export default {
  data() {
    let edytowanie = false;
    let daneZbiorki;
    console.log(this.daneskladka);
    return {
      edytowanie,
      daneZbiorki,
    };
  },

  name: "WierszSkladki",
  props: ["daneskladka"],
};
</script>
