<template>
  <div class="TabelaTel">
    <div class="col">
      <input
        v-model="szukane"
        type="text"
        class="form-control-dark"
        style="margin: 10px"
        placeholder="Wyszukaj Osoby"
      />
    </div>
    <table class="table table-bordered table-striped table-dark">
      <thead>
        <tr>
          <th scope="col">Osoby Które Nie Zapłaciły</th>
        </tr>
      </thead>
      <tbody>
        <div v-for="k in listaOsob" :key="k.id">
          <div
            v-if="
              wplaty.filter(
                (e) => e.uczen_id == k.id && e.skladka_id == wplatySkladka.id
              ).length == 0
            "
          >
            <button
              type="button"
              class="btn btn-sm btn-dark"
              style="margin: 10px"
              v-on:click="idUczniaWplacajacego = k.id"
              v-if="idUczniaWplacajacego != k.id"
            >
              {{ k.Imie }}
              {{ k.Nazwisko }}
            </button>
            <div v-if="idUczniaWplacajacego == k.id">
              <input
                v-model="kwotaDane"
                type="text"
                class="form-control-dark"
                placeholder="Kwota Wpłaty"
              />
              <button
                type="button"
                class="btn btn-sm btn-dark"
                style="margin: 10px"
                v-on:click="
                  $emit('zaplacil', k, wplatySkladka, kwotaDane),
                    (idUczniaWplacajacego = ''),
                    (kwotaDane = '')
                "
              >Zapisz</button>
              <button
                type="button"
                class="btn btn-sm btn-dark"
                style="margin: 10px"
                v-on:click="idUczniaWplacajacego = false"
              >Anuluj</button>
            </div>
          </div>
        </div>
      </tbody>
    </table>
    <table class="table table-bordered table-striped table-dark">
      <thead>
        <tr>
          <th scope="col">Osoby Które Zaplaciły</th>
        </tr>
      </thead>
      <tbody>
        <div v-for="k in listaOsob.filter(walonefiltrowanie)" :key="k.id">
          <span
            v-if="
              wplaty.filter(
                (e) => e.uczen_id == k.id && e.skladka_id == wplatySkladka.id
              ).length > 0
            "
          >
            <div
              v-for="wplata in wplaty.filter(
                (e) => e.uczen_id == k.id && e.skladka_id == wplatySkladka.id
              )"
              :key="wplata.id"
            >
              <button
                v-if="idUczniaWplacajacego != k.id && edytowanie == false"
                type="button"
                class="btn btn-dark"
                v-on:click="idUczniaWplacajacego = k.id"
              >
                {{ k.Imie }}
                {{ k.Nazwisko }}
                {{ wplata.kwota | currencyFormat }}
                [ Data wpłaty : {{ wplata.data }} ]
              </button>

              <button
                v-if="idUczniaWplacajacego != k.id && edytowanie == false"
                type="button"
                class="btn btn-dark"
                v-on:click="(edytowanie = true), (idUczniaWplacajacego = k.id), (kwotaDane=wplata.kwota)"
              >
                Edytuj
              </button>

              <div v-if="idUczniaWplacajacego == k.id && edytowanie == true">
                <input
                  v-model="kwotaDane"
                  type="text"
                  class="form-control-dark"
                  placeholder="Edycja Kwoty"
                />
                <button
                  type="button"
                  class="btn btn-sm btn-dark"
                  style="margin: 10px"
                  v-on:click="
                    $emit('edytowanie', wplata, kwotaDane),
                      (idUczniaWplacajacego = ''),
                      (kwotaDane = ''),
                      (edytowanie = false)
                  "
                >Zapisz</button>
                <button
                  type="button"
                  class="btn btn-sm btn-dark"
                  style="margin: 10px"
                  v-on:click="(idUczniaWplacajacego = ''), (edytowanie = false)"
                >Anuluj</button>
              </div>

              <div v-if="idUczniaWplacajacego == k.id && edytowanie == false">
                <div>Usunąć?</div>
                <button
                  type="button"
                  class="btn btn-sm btn-dark"
                  style="margin: 10px"
                  v-on:click="
                    $emit('usun', wplata), (idUczniaWplacajacego = '')
                  "
                >
                  Tak
                </button>
                <button
                  type="button"
                  class="btn btn-sm btn-dark"
                  style="margin: 10px"
                  v-on:click="idUczniaWplacajacego = ''"
                >
                  Nie
                </button>
              </div>
            </div>
          </span>
        </div>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    let kwotaDane = "";
    let wpisKwoty = false;
    let szukane = "";
    let edytowanie = false;
    return {
      szukane,
      idUczniaWplacajacego: wpisKwoty,
      kwotaDane,
      edytowanie,
    };
  },
  name: "TabelaWplat",
  props: ["listaOsob", "wplaty", "wplatySkladka"],
};
</script>
