<template>
  <tr v-if="edytowanie == false" style="margin: 10px">
    <td>{{ daneosoba.Imie }}</td>
    <td>{{ daneosoba.Nazwisko }}</td>
    <td>{{ daneosoba.Telefon }}</td>
    <td>
      <button
        type="button"
        class="btn btn-sm btn-dark"
        style="margin: 10px"
        v-on:click="$emit('usun')"
      >
        Usuń
      </button>
      <button
        type="button"
        class="btn btn-sm btn-dark"
        style="margin: 10px"
        v-on:click="(edytowanie = true), (clondaneosoba = daneosoba)"
      >
        Edytuj
      </button>
      
    </td>
  </tr>

  <tr class="info" v-else>
    <td>
      <input
        v-model="clondaneosoba.Imie"
        type="text"
        class="form-control-dark"
        style="margin: 10px"
        placeholder="Imie"
      />
    </td>
    <td>
      <input
        v-model="clondaneosoba.Nazwisko"
        type="text"
        class="form-control-dark"
        style="margin: 10px"
        placeholder="Nazwisko"
      />
    </td>
    <td>
      <input
        v-model="clondaneosoba.Telefon"
        type="text"
        class="form-control-dark"
        style="margin: 10px"
        placeholder="Numer Telefonu"
      />
    </td>
    <td>
      <button
        type="button"
        class="btn btn-sm btn-dark"
        style="margin: 10px"
        v-on:click="$emit('zapisz', clondaneosoba), (edytowanie = false)"
      >
        Zapisz
      </button>
    </td>
    <td></td>
  </tr>
</template>

<script>
export default {
  data() {
    let edytowanie = false;
    let clondaneosoba;
    console.log(this.daneosoba);
    return {
      edytowanie,
      clondaneosoba,
    };
  },
  name: "TabelaOsob",
  props: ["daneosoba"],
};
</script>
